import React from 'react'
import Marquee from "react-fast-marquee";
import "./Skills.css";
import { skillsData } from "./SkillData";

function Skills() {
  // const { theme } = useContext(ThemeContext);

  const skillBoxStyle = {
    backgroundColor: "#",
    // boxShadow: `0px 0px 30px `,
    // style={{ backgroundColor: theme.secondary }}
  };

  return (
    <div  id="Skills" >
      <div className="skillsHeader">
        <h2 style={{color:'#DC143C'}}>Skills</h2>
      </div>
      <div className="skillsContainer">
        <div className="skill--scroll">
          <Marquee
            gradient={false}
            speed={80}
            pauseOnHover={true}
            pauseOnClick={true}
            delay={0}
            play={true}
            direction="left"
          >
            {skillsData.map((skill, id) => (
              <div className="skill--box" key={id} style={{color:"#eee"}}>
                <img src={skill.image} alt={skill} />
                <h3 style={{ color: "#000" }}>{skill.skill}</h3>
              </div>
            ))}
          </Marquee>
        </div>
      </div>
    </div>
  );
}

export default Skills;