import { ReactNode } from 'react';
import Avnish_Kumar_Resume from '../Resume/Avnish_Kumar_Resume.pdf'
import {
  Box,
  Flex,
  Avatar,
  HStack,
  IconButton,
 
  Button,
  Menu,
  MenuButton,
 
  useDisclosure,
  useColorModeValue,
  Stack,
  Heading,
 
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
import { Link } from 'react-scroll';
// const Links = ['Skills', 'Project', 'Contact'];

const NavLink = ({ children }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
      bg: useColorModeValue('gray.200', 'gray.700'),
    }}
    href={'#'}>
    {children}
  </Link>
);

export default function Simple() {
  const { isOpen, onOpen, onClose } = useDisclosure();

  return (
    <>
      <Box bg={useColorModeValue('#DC143C', '#DC143C')}  px={4} zIndex='999' position="fixed" top='0' overflow='hidden'  w='100%'>
        <Flex h={16} alignItems={'center'} justifyContent={'space-between'}>
          <IconButton
            size={'md'}
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={'Open Menu'}
            display={{ md: 'none',}}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={8} alignItems={'center'}>
            {/* <Box>Logo</Box> */}
            <HStack
              as={'nav'}
              spacing={4}
              
              display={{ base: 'none', md: 'flex'}}>
              
         
            <Link to='home'>
                <Heading as='h4' size='md' color={'#EEEEEE'} smoth={true} duration={1000}>Home</Heading>
              </Link>
              <Link  to='Skills'>
              <Heading as='h4' size='md' color={"#EEEEEE"} smoth={true} duration={2000}>Skill</Heading>
              </Link>
              <Link  to='Project'>
              <Heading as='h4' size='md' color={"#EEEEEE"} smoth={true} duration={2000}>Project</Heading>
              </Link>
              <Link  to='About'>
              <Heading as='h4' size='md'color={"#EEEEEE"} smoth={true} duration={2000}>About</Heading>
              </Link>
              <Link  to='Contact'>
              <Heading as='h4' size='md' color={"#EEEEEE"} smoth={true} duration={1000}>Contact</Heading>
              </Link>
              <Link  to='Github'>
              <Heading as='h4' size='md' color={"#EEEEEE"} smoth={true} duration={1000}>Github</Heading>
              </Link>
              {/* <Link  > */}
              <Heading as='h4' size='md' color={"#EEEEEE"} smoth={true} duration={1000}>
              <a  href={Avnish_Kumar_Resume} download={'Avnish_Kumar_Resume'}>
              <p onClick={()=>{window.open("https://drive.google.com/file/d/1Qa3keHQrImBJGk18fx04EoO7KYLXLJ_a/view?usp=sharing","_blank")}}>Resume</p>
              </a>
              </Heading>
              {/* </Link> */}
            
 
            </HStack>
          </HStack>
          <Flex alignItems={'center'}>
            <Menu>
              <MenuButton
                as={Button}
                rounded={'full'}
                variant={'link'}
                cursor={'pointer'}
                minW={0}>
                <Avatar
                  size={'sm'}
                  src={
                    'https://avatars.githubusercontent.com/u/101566216?s=40&v=4'
                  }
                />
              </MenuButton>
              {/* <MenuList>
                <MenuItem>Link 1</MenuItem>
                <MenuItem>Link 2</MenuItem>
                <MenuDivider />
                <MenuItem>Link 3</MenuItem>
              </MenuList> */}
            </Menu>
          </Flex>
        </Flex>

        {isOpen ?  (
          <Box pb={4} display={{ md: 'none' }}  >
            <Stack as={'nav'} spacing={4}   >
            
              <Link to='home' >
                <Heading as='h4' size='md' color={'#000'} smoth={true} duration={2000} onClick={ onClose }>Home</Heading>
              </Link>
              <Link  to='Skills'>
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={2000} onClick={ onClose }>Skill</Heading>
              </Link>
              <Link  to='Project'>
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={2000} onClick={ onClose }>Project</Heading>
              </Link>
              <Link  to='About'>
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={2000} onClick={ onClose }>About</Heading>
              </Link>
              <Link  to='Contact'>
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={2000} onClick={ onClose }>Contact</Heading>
              </Link>
              <Link  to='Github'>
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={3000} onClick={ onClose }>Github</Heading>
              </Link>
            
              <Heading as='h4' size='md' color={'#000'} smoth={true} duration={1000} onClick={ onClose }>
              <a  href={Avnish_Kumar_Resume} download={'Avnish_Kumar_Resume'}>
              <p onClick={()=>{window.open("https://drive.google.com/file/d/1R2GCDN-vQVtf1RdrMkBLNTPABS8U51qe/view?usp=sharing","_blank")}}>Resume</p>
              </a>
              </Heading>
              
            </Stack>
           
 
          </Box>
        ) :null}
      </Box>

      {/* <Box p={4}>Main Content Here</Box> */}
    </>
  );
}